"use client"

import { useState } from "react"
import { useIncidents } from "@/hooks/useIncidents"
import IncidentTable from "@/features/incidents/components/IncidentTable"
import IncidentModal from "@/features/incidents/components/IncidentModal"
import { Button } from "@/components/ui/button"
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from "@/components/ui/dialog"

export default function HomePage() {
    const { data, isLoading, error } = useIncidents()
    const [showModal, setShowModal] = useState(false);
    const [isOpen, setIsOpen] = useState(false)

    return (
        <div className="p-6">
            <div className="flex justify-between items-center mb-4">
                <h1 className="text-red-600 text-lg">Incident Reports</h1>

                <Dialog open={isOpen} onOpenChange={setIsOpen}>
                    <DialogTrigger asChild>
                        <Button>+ Create</Button>
                    </DialogTrigger>

                    <DialogContent className="sm:max-w-[600px]">
                        <DialogHeader>
                            <DialogTitle>Create Incident</DialogTitle>
                        </DialogHeader>

                        {/* IncidentModal is your form */}
                        <IncidentModal open={isOpen} onClose={() => setIsOpen(false)} />
                    </DialogContent>
                </Dialog>
            </div>

            {isLoading && <p>Loading...</p>}
            {error instanceof Error && (
                <p className="text-red-600">
                    Failed to load incidents: {error.message}
                </p>
            )}
            {data && <IncidentTable data={data} />}
        </div>
    )
}
