"use client";

import { useState } from "react";
import { useCreateIncident } from "@/hooks/useIncidents";
import {
    Dialog,
    DialogContent,
    DialogHeader,
    DialogTitle,
    DialogFooter,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";

interface Props {
    onClose: () => void;
    open: boolean;
}

export default function IncidentModal({ onClose, open }: Props) {
    const { mutate, isPending } = useCreateIncident();

    const [form, setForm] = useState<{
        description: string;
        classification: string;
        status: "Open" | "Ongoing" | "Closed";
    }>({
        description: "",
        classification: "",
        status: "Open",
    });

    const handleChange = (name: string, value: string) => {
        setForm((prev) => ({
            ...prev,
            [name]: name === "status" ? (value as "Open" | "Ongoing" | "Closed") : value,
        }));
    };

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        mutate(form, { onSuccess: onClose });
    };

    return (
        <Dialog open={open} onOpenChange={onClose}>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>File Incident Report</DialogTitle>
                </DialogHeader>

                <form onSubmit={handleSubmit} className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="description">Description</Label>
                        <Input
                            id="description"
                            name="description"
                            placeholder="Description"
                            value={form.description}
                            onChange={(e) => handleChange("description", e.target.value)}
                            required
                        />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="classification">Classification</Label>
                        <Input
                            id="classification"
                            name="classification"
                            placeholder="Classification"
                            value={form.classification}
                            onChange={(e) => handleChange("classification", e.target.value)}
                            required
                        />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="status">Status</Label>
                        <Select
                            value={form.status}
                            onValueChange={(value) => handleChange("status", value)}
                        >
                            <SelectTrigger>
                                <SelectValue placeholder="Select status" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="Open">Open</SelectItem>
                                <SelectItem value="Ongoing">Ongoing</SelectItem>
                                <SelectItem value="Closed">Closed</SelectItem>
                            </SelectContent>
                        </Select>
                    </div>

                    <DialogFooter>
                        <Button type="button" variant="outline" onClick={onClose}>
                            Cancel
                        </Button>
                        <Button type="submit" disabled={isPending}>
                            {isPending ? "Saving..." : "Submit"}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}
